@import 'variables';

//mixins for Gutenberg styling purpose
@mixin qodeBody() {
	font-family: 'Raleway', sans-serif;
	font-size: 14px;
	line-height: 26px;
	color: #818181;
	font-weight: 400;
	background-color: #f6f6f6;
	overflow-x: initial !important;
	-webkit-font-smoothing: antialiased;
}

@mixin qodeTypographyLayoutImportant(){
	color: inherit !important;
	font-family: inherit !important;
	font-size: inherit !important;
	font-weight: inherit !important;
	font-style: inherit !important;
	line-height: inherit !important;
	letter-spacing: inherit !important;
	text-transform: inherit !important;
}

@mixin qodeDefaultHeadingStyle() {

	margin: 0;
	color: #303030;

	a {
		color: #303030;
		@include qodeTransition(color 0.1s linear);

		&:hover {
			color: #1abc9c;
		}
	}
}

@mixin qodeH1() {
	font-size: 30px;
	line-height: 1.25em; /* 20px/16px */
	font-weight: 700;
}

@mixin qodeH2() {
	font-size: 24px;
	line-height: 1.384615384615385em;  /* 36px/26px */
	text-transform: uppercase;
	letter-spacing: 1px;
	font-weight: 600;
}

@mixin qodeH3() {
	font-size: 17px;
	line-height: 1.304347826086957em; /* 30px/23px */
	text-transform:uppercase;
	letter-spacing:1px;
	font-weight: 600;
}

@mixin qodeH4() {
	font-size: 19px;
	line-height: 1.375em; /* 22px/16px */
	font-weight: 400;
}

@mixin qodeH5() {
	font-size: 15px;
	line-height: 1.571428571428571em; /* 22px/14px */
	text-transform:uppercase;
	letter-spacing:1px;
	font-weight: 600;
}

@mixin qodeH6() {
	font-size: 13px;
	line-height: 1.538461538461538em; /* 20px/13px */
	text-transform:uppercase;
	letter-spacing:1px;
	font-weight:600;
}

@mixin qodeDefaultButton(){
	position: relative;
	display: inline-block;
	width: auto;
	height: 39px;
	line-height: 39px;
	margin: 0;
	padding: 0px 23px;
	border: 2px solid #303030;
	font-size: 13px;
	font-weight: 700;
	font-family: inherit;
	text-align: left;
	color: #303030;
	text-decoration: none;
	cursor: pointer;
	white-space: nowrap;
	outline: none;
	font-style: normal;
	text-transform: uppercase;
	letter-spacing: 1px;

	-o-border-radius: 4px;
	-moz-border-radius: 4px;
	-webkit-border-radius: 4px;
	-ms-border-radius: 4px;
	border-radius: 4px;
	text-shadow: none;

	background-color: transparent;
	-webkit-transition: color 0.1s linear, background-color 0.1s linear,border-color 0.1s linear;
	-moz-transition: color 0.1s linear, background-color 0.1s linear,border-color 0.1s linear;
	-ms-transition: color 0.1s linear, background-color 0.1s linear,border-color 0.1s linear;
	-o-transition: color 0.1s linear, background-color 0.1s linear,border-color 0.1s linear;
	transition: color 0.1s linear, background-color 0.1s linear,border-color 0.1s linear;

	-webkit-box-sizing: initial !important;
	-moz-box-sizing: initial !important;
	box-sizing: initial !important;
}

@mixin qodeDefaultButtonHover(){
	background-color: #1abc9c;
	border-color: #1abc9c;
	color: #fff;
	text-decoration: none;
}

@mixin qodeBlockquote($important : null) {
	@include qodeRelativeHolderLayout();
	padding: 14px 14px 14px 10px $important;
	margin: 0;
	text-align: left;
	quotes: none;
	box-sizing: border-box;
	border: none $important;
	color: $default-text-color $important;

	> * {
		@include qodeTypographyLayout();
		margin: 0;
	}

	&:before,
	&:after {
		content: '';
	}

	cite,
	.wp-block-quote__citation,
	.wp-block-pullquote__citation,
	footer {
		display: block $important;
		margin-top: 10px $important;
		text-align: inherit $important;
		font-size: 14px $important;
		line-height: 1.3em $important;
		letter-spacing: 0 $important;
		font-style: normal  $important;
		font-weight: 400 $important;
		text-transform: none $important;
	}

}

@mixin qodefPlaceholder {
	&::-webkit-input-placeholder {
		@content
	}

	&:-moz-placeholder {
		@content;

		opacity: 1;
	}
	&::-moz-placeholder {
		@content;

		opacity: 1;
	}
	&:-ms-input-placeholder {
		@content
	}
}

//layout mixins - start

@mixin qodeTableLayout(){
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin qodeTableCellLayout(){
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: middle;
    text-align: center;
}

@mixin qodeRelativeHolderLayout() {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: middle;
}

@mixin qodeAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin qodeTypographyLayout(){
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}
@mixin qodeBoxShadowCustom($box-shadow) {
	-webkit-box-shadow: $box-shadow;
	-moz-box-shadow: $box-shadow;
	box-shadow: $box-shadow;
}
//layout mixins - end

//transition mixins - start

@mixin qodeTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}
@mixin qodeTransitionTransform($transition-param...) {
	-webkit-transition: -webkit-transform $transition-param;
	-moz-transition: -moz-transform $transition-param;
	transition: transform $transition-param;
}
@mixin qodeTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin qodeAnimation($animation-param...) {
	-webkit-animation: $animation-param;
    -moz-animation: $animation-param;
	animation: $animation-param;
}

@mixin qodeTransformOrigin($animation-param...) {
	-webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

@mixin qodeBoxShadow($shadow-property...) {
    -webkit-box-shadow: $shadow-property;
    -moz-box-shadow: $shadow-property;
    box-shadow: $shadow-property;
}

@mixin qodeBorderRadius($border-radius...) {
    -webkit-border-radius: $border-radius;
    -moz-border-radius: $border-radius;
    border-radius: $border-radius;
}

@mixin qodeBoxSizing($box-sizing) {
    -webkit-box-sizing: $box-sizing;
    -moz-box-sizing: $box-sizing;
    box-sizing: $box-sizing;
}
@mixin qodeAbsoluteHolderLayout() {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
@mixin qodeFlexContainer($flex-direction: row, $flex-wrap: nowrap, $justify-content: flex-start, $align-items: stretch, $align-content: stretch ){
	display:flex;
	flex-direction: $flex-direction;
	flex-wrap: $flex-wrap;
	justify-content: $justify-content;
	align-items: $align-items;
	align-content: $align-content;
}

@mixin qodeFlexItem($order: 0, $flex-grow: 0, $flex-shrink: 1, $flex-basis: auto){
	order: $order;
	flex-grow: $flex-grow;
	flex-shrink: $flex-shrink;
	flex-basis: $flex-basis;
}
@mixin qodeImageOverlayHoverStyle($with-hover: true){

	@if ($with-hover) {

		&:hover {

			&:after {
				opacity: 1;
			}
		}

		&:after {
			@include qodeAbsoluteHolderLayout();
			content: '';
			background-color: rgba($default-heading-color, .4);
			opacity: 0;
			@include qodeTransition(opacity .2s ease-in-out);
		}

	} @else {
		@include qodeAbsoluteHolderLayout();
		content: '';
		background-color: rgba($default-heading-color, .4);
		opacity: 0;
		@include qodeTransition(opacity .2s ease-in-out);
	}
}
//transition mixins - end

