table.tt_timetable {
	font-weight: 400;
	color: #808080;
	font-size: 15px;
	font-family: inherit;
	border-collapse: collapse !important; //plugin is also using important

	border: 1px solid #e5e5e5;

	thead tr {
		border: 1px solid #e5e5e5;
	}

	tbody tr {
		border: 1px dashed #e5e5e5;
	}

	th {
		font-size: 19px;
		color: #262626;
		height: 75px;
		border-right: 1px solid #e5e5e5 !important; //plugin is also using important
		box-sizing: border-box;
	}

	td {
		color: #808080;
		font-size: 15px;
		border-right: 1px solid #e5e5e5 !important;
	}

	.row_gray {
		background-color: transparent !important; //important because plugin css is also using important
	}

	.event_container {
		padding: 27px 10px;
	}

	tbody tr:nth-child(even) {
		background-color: #fff !important;
	}

	tbody tr:nth-child(odd) {
		background-color: #fafafa !important;;
	}

	.event {
		background-color: transparent;
		color: #808080;

		a, .event_header {
			color: $first-main-color !important; //important because plugin scss is also using important
		}

		.after_hour_text,
		.before_hour_text{
			font-size: 15px;
			color: #808080 !important;
		}

		.event_header {
			font-size: 16px;

			&:not(:only-child){
				margin-bottom: 18px;
			}
		}

		.event_container .hours {
			color: #808080;
		}

		&:not(.tt_single_event) {
			.event_container {
				border-bottom: 1px dashed #e5e5e5;

				&:last-child {
					border-bottom: none;
				}
			}
		}

		hr {
			display: none;
		}

		&:hover, .event_container.tt_tooltip:hover {
			background-color: transparent;
		}

		&.tt_tooltip:hover .hours, .event_container.tt_tooltip:hover .hours {
			color: #808080;
		}

		a:hover, a.event_header:hover {
			text-decoration: none;

			color: #303030 !important;
		}
	}

	.tt_tooltip_content {
		color: #888 !important;
		background: #fff;
		text-align: center;

		padding: 15px;
		font-size: 13px;

		box-sizing: border-box;
		border: 1px solid #e5e5e5;
		@include qodeBorderRadius(10px);
	}

	.event .tt_tooltip_content a {
		position: relative;
		left: 1px; //for content to be in the middle
		color: #000 !important;
		font-size: 16px;
		font-weight: 700;
		margin-bottom: 8px;

		&:hover {
			color: #888 !important;
		}
	}

	.tt_tooltip .tt_tooltip_arrow {
		border-color: #fff transparent;

		border-width: 16px 16px 0;
		bottom: -17px;

		&:before{
			position: absolute;
			content: '';
			border-style: solid;
			border-color: #e5e5e5 transparent;
			border-width: 17px 17px 0;
			bottom: calc(100% - 1px);
			left: 0;
			transform: translateX(-50%);
			z-index: -1;
		}
	}
}

.tt_responsive .tt_timetable.small {
	.box_header {
		color: #808080 !important;
		font-size: 20px !important;
		text-align: left;

		margin-bottom: 20px;
	}

	.tt_items_list {
		li {
			border-color: #e5e5e5;

			padding: 15px 0 !important;
		}

		.event_container {
			text-align: left;
		}

		a,
		.value {
			font-weight: 700;
			font-size: 16px;
			font-family: inherit;
			color: #808080 !important;
		}
	}
}

.tt_tabs {
	text-align: center;

	.tt_tabs_navigation {
		display: inline-block;
		width: auto !important;

		margin-bottom: 15px;

		li {
			margin-right: 15px !important;
			margin-bottom: 15px !important;

			float: none;
			display: inline-block;
		}

		li a {
			color: #fff !important;
			background-color: $first-main-color;
			font-size: 14px;
			font-weight: 700;
			font-family: inherit;
			

			border: 2px solid $first-main-color !important;
			padding: 5px 15px !important;

			@include qodeBorderRadius(30px);
			box-sizing: border-box;
			@include qodeTransition(color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important);

		}

		.ui-tabs-active a,
		li a:hover {
			background-color: transparent;
			border: 2px solid $first-main-color !important;
			color: $first-main-color !important;
		}
	}
}

.tabs_box_navigation.sf-timetable-menu{
	font-family: inherit;
	
	.tabs_box_navigation_selected{
		border-color: $first-main-color;
		background-color: $first-main-color;
	}

	li:hover ul a,
	li.submenu:hover ul a{
		font-family: inherit;
	}

	li ul li a:hover,
	li ul li.selected a:hover{
		background-color: $first-main-color;
	}
}