.qode-ttevents-single {
	margin-bottom: 120px;
	
	.qode-ttevents-single-image-holder {
		margin-bottom: 53px;

		img {
			display: block;
		}
	}

	.qode-ttevents-single-title {
		margin-top: 0;
		margin-bottom: 10px;
	}

	.qode-ttevents-single-subtitle {
		margin-top: 0;
	}

	.qode-ttevents-single-content {
		margin-top: 35px;
	}

	.tt_event_hours{
		margin-top: 27px;

		li {
			border: 1px solid #e5e5e5;
			background-color: #fafafa;
			padding: 27px 35px 17px;
			margin-bottom: 3%;
			width: 48.5%;
			margin-right: 3%;
			box-sizing: border-box;

			&.event_hours_right {
				margin-right: 0;
			}

			h4 {
				color: #545454;
				margin: 0;
				font-size: 20px;

				&:nth-of-type(1) {
					margin-bottom: 6px;
				}
			}

			p {
				margin-top: 7px;
				font-weight: 700;
				color: #ababab;
				font-size: 16px;
			}
		}
	}

	.tt_event_items_list {
		margin: 0;

		li.type_info {
			border-bottom: 1px dashed #e5e5e5;

			padding: 10px 0;

			.tt_event_text {
				color: $first-main-color;
				font-size: 15px;
				font-weight: 400;
			}

			label {
				font-size: 16px !important;
				font-weight: 600 !important;
				color: #303030;
			}
		}

		li:not(.type_info) {
			background: none;
			font-size: 15px;
			color: #808080;

			margin-bottom: 19px;
			padding-left: 27px;

			position: relative;

			&:before {
				content: '\4e';
				position: absolute;
				left: 0;

				font-family: 'ElegantIcons';
				color: $first-main-color;
				font-size: 18px;
			}
		}
	}
}